import { vertexShaderSource, fragmentShaderSource } from "./shaders/basicshaders.js"
import { cubeVertices, cubeFaceColors, cubeIndices, cubeTextCoords } from "./mesh/basicmesh.js"
import { mat4 } from "gl-matrix"
import { ShaderProgram } from "./shaders/ShaderProgram.js";
import { Mesh } from "./mesh/Mesh.js";
import { Texture } from "./textures/Texture.js";

// options
let FPS_OUTPUT = false;

// get canvas and webgl context
const canvas = document.querySelector('#canvas');
const gl = canvas.getContext('webgl2');

// If we don't have a GL context, give up now
if (!gl) {
  alert('Unable to initialize WebGL. Your browser or machine may not support it.');
  return;
}

// create basic shader
const shader = new ShaderProgram(gl, "BasicShader", vertexShaderSource, fragmentShaderSource);

// load texture
const texture = new Texture(gl, "./images/block.png"/*"https://mineblocks.com/1/wiki/images/6/67/Cobblestone.png"*/);

// load positions and colors into a mesh
const mesh = new Mesh(gl, texture, cubeVertices, cubeTextCoords, cubeIndices);

console.log("Finished startup!");

function resize(gl) {
  // resize
  canvas.width = gl.canvas.clientWidth;
  canvas.height = gl.canvas.clientHeight;
  gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);
}

let rotation = 0;
function drawScene(gl, shader, mesh, deltaTime) {
  // call resize
  resize(gl);

  rotation += deltaTime;
  if (FPS_OUTPUT) console.log("FPS " + (1 / deltaTime));

  // clear everything
  gl.clearColor(0.0, 0.0, 0.0, 1.0);                    // Clear to black, fully opaque
  gl.clearDepth(1.0);                                   // Clear everything
  gl.enable(gl.DEPTH_TEST);                             // Enable depth testing
  gl.depthFunc(gl.LEQUAL);                              // Near things obscure far things
  gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);  // Clear color and depth bits

  // create projection information
  const fov = 45 * Math.PI / 180;
  const aspect = gl.canvas.clientWidth / gl.canvas.clientHeight;
  const zNear = 0.1;
  const zFar = 100.0;
  const projectionMatrix = mat4.create();

  // create projection matrix
  mat4.perspective(projectionMatrix, fov, aspect, zNear, zFar);

  // create model view matrix
  const modelViewMatrix = mat4.create();
  mat4.translate(modelViewMatrix, modelViewMatrix, [-0.0, 0.0, -6.0]);
  mat4.rotateX(modelViewMatrix, modelViewMatrix, rotation);
  mat4.rotateY(modelViewMatrix, modelViewMatrix, rotation);
  mat4.rotateZ(modelViewMatrix, modelViewMatrix, rotation);

  // draw mesh
  shader.draw(gl, mesh, projectionMatrix, modelViewMatrix);
}

let then = 0;
function render(now) {
  now *= 0.001; // convert to seconds
  const deltaTime = now - then; // get delta time
  then = now;  // update then time variable for delta time tracking

  drawScene(gl, shader, mesh, deltaTime);

  requestAnimationFrame(render);
}
requestAnimationFrame(render);